body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(249, 249, 249);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-container {
  height: 120px;
  margin-top: 15px; }
  .form-container .ant-form-explain {
    font-size: 10px;
    margin-top: 5px; }
  .form-container .input {
    width: 100%; }

.divider {
  margin: 12px 0; }

.card-container {
  padding: 30px 30px;
  margin: 30px auto;
  background-color: white;
  box-shadow: 0px 0px 15px #e6e6e6;
  width: 400px;
  min-height: 654px; }
  .card-container .title {
    font-weight: bold;
    font-size: 20px; }
  .card-container .button {
    margin-top: 10px; }
  .card-container .alert {
    margin-top: 10px;
    font-size: 10px; }
  .card-container .tag {
    margin-right: 0; }

.logo {
  width: 30px;
  height: 30px;
  margin-right: 10px; }

.logo-text {
  color: white;
  font-weight: bold;
  font-size: 20px; }

.page-layout {
  height: 100vh;
  min-height: 810px; }

.header-row {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center; }

